

    .add-form {
        display: flex;
        margin-bottom: 10px;

        input {
            flex: 1;
            margin-right: 10px;
            padding: 10px;
        }
    }

    .block-list {
        position: relative;
        border: 1px solid #d4d4d4;
        min-height: 100px;
        max-height: 250px;
        overflow: auto;

        .no-blocks-message {
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            text-align: center;
            display: flex;
            justify-content: center;
            align-items: center;
            color: #808080;
        }

        .block-item {
            display: flex;
            padding: 10px 15px;

            &.animate {
                animation: blockItemIn 1s;
            }

            @keyframes blockItemIn {
                from {
                    background: #cccccc;
                }
                to {
                    background: transparent;
                }
            }

            &:hover {
                background: #f0f0f0;
            }

            div:first-child {
                flex: 1;
            }
        }
    }

