
    .keyboard-shortcut-picker {
        position: fixed;
        z-index: 10;
        top: 0; left: 0; right: 0; bottom: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background: rgba(0, 0, 0, 0.8);
        color: #FFF;
        animation: pickerIn .8s;
        animation-timing-function: cubic-bezier(0.075, 0.820, 0.165, 1.000); /* easeOutCirc */

        .big {
            font-size: 40px;
            font-weight: 300;
            margin: 0 0 60px;
        }

        .actions {
            margin-bottom: 100px;
        }

        button {
            padding: 10px 20px;
            font-size: 16px;
            margin: 0 10px;
        }

        @keyframes pickerIn {
            from {
                transform: scale(1.2);
                opacity: 0;
            }
        }
    }
